import React from "react";

const WallkiWork = () => {
    return (
        <div className="content-wrapper">
            <div className="wallki-work-wrapper">
                <div className="content">
                    <h2 className="content-text-heading">Wallki.</h2>
                    <h3 className="content-text-subheading">
                        A small social network that allows you to distribute interesting content to your friends.
                    </h3>
                </div>
                <div className="poster">
                    <img src="static/img/stack/react.png" className="react-icon" alt="" />
                    <img src="static/img/stack/semantic.png" className="semantic-icon" alt="" />
                    <div className="card">
                        <img className="card-img" src="static/img/works/wallki.png" alt="" />
                        <div className="card-description">
                            <a href="http://wallki.gherciu.gq" target="_blank" className="card-btn">
                                <img src="static/img/icons/see.svg" />
                                View demo
                            </a>
                        </div>
                    </div>
                </div>
            </div>
            <style jsx>{`
                .react-icon {
                    max-width: 500px;
                    height: auto;
                    position: absolute;
                    bottom: -140px;
                    left: 120px;
                    transform: rotate(50deg);
                }
                .semantic-icon {
                    max-width: 200px;
                    height: auto;
                    position: absolute;
                    top: 40px;
                    right: -80px;
                    transform: rotate(20deg);
                }

                .wallki-work-wrapper {
                    display: flex;
                    justify-content: space-between;
                    margin-bottom: 0px;
                    align-items: center;
                }
                h3 {
                    margin-top: 10px;
                }
                .content {
                    max-width: 390px;
                }
                .poster {
                    max-width: 560px;
                    width: 100%;
                    padding: 20px 0px;
                    display: flex;
                    justify-content: center;
                    position: relative;
                }
                .card {
                    max-width: 480px;
                    z-index: 2;
                }
                @media (max-width: 860px) {
                    .wallki-work-wrapper {
                        flex-direction: column;
                    }
                    .content-text-heading {
                        text-align: center;
                    }
                    .content-text-subheading {
                        text-align: center;
                    }
                    .semantic-icon {
                        display: none;
                    }
                    .react-icon {
                        display: none;
                    }
                }
            `}</style>
        </div>
    );
};

export default WallkiWork;
